<template>
  <div id="write">
    <header>
    <span style="text-align:left">
      
    </span>
    <el-input v-model="title" placeholder="" style="width:300px"></el-input>
    <span style="margin-left:150px">
      
    </span>
    <el-select v-model="typeValue" placeholder="" style="width:200px">
      <el-option
        v-for="item in type"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    
    <el-select v-model="techTypeValue" placeholder="" style="width:200px">
      <el-option
        v-for="item in techType"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    </header>

    <div class="indexContainer">
      <div class="btnsContainer">
        <!-- <div class="btn" @click="getMdValueFn">mdValue</div> -->
        <el-button type="primary" @click="addArticle"></el-button>
      </div>
      <!-- <div class="maskContainer" v-if="dilogStatus">
        <div class="contentContainer">
        <div class="closeBtnContainer" @click="closeMaskFn"></div>
          <textarea class="showAreaContainer" v-model="msgShow" readonly></textarea>
        </div>
      </div> -->
      <div class="editorContainer">
        <markdown 
        :mdValuesP="msg.mdValue"  
        :fullPageStatusP="false" 
        :editStatusP="true" 
        :previewStatusP="true" 
        :navStatusP="true"
        :icoStatusP="true"  
        @childevent="childEventHandler"
        ></markdown>
        <!--P-->
        <!--:mdValuesP="msg" -->
        <!--:fullPageStatusP="false" true/false-->
        <!--:editStatusP="false" true/false-->
        <!--:previewStatusP="false" true/false-->
        <!--:navStatusP="false" true/false-->
        <!--:icoStatusP="false" true/false,-->
        <!--@childevent="childEventHandler" $.emit()-->
      </div>
    </div>
  </div>
</template>

<script>
import markdown from './article/MarkDown';
import Axios from 'axios';
import state from '../store/state';

export default {
  name: 'index',
  data() {
    return {
      msg: {
        mdValue: state.article.data.md_content
      },
      type: state.type,
      typeValue: state.article.data.type,
      techType: state.techType,
      techTypeValue: state.article.data.tech_type,
      title: state.article.data.title,
      isEdit: false
    };
  },
  components: {
    markdown
  },
  computed: {
    
  },
  methods: {
    childEventHandler: function(res) {
      // resdata,mdValuehtmlValue
      this.msg = res;
    },
    addArticle() {
      Axios.post('http://www.ftusix.com/static/data/writeArticle.php', {
        content: this.msg.htmlValue, //  html
        md_content: this.msg.mdValue, //  md
        user_id: state.user[0].user_id, //  id
        nickname: state.user[0].nick_name, //  ,
        // token: state.user[0].token, 
        type: this.typeValue, //   0. 1. 2. 3.,
        tech_type: this.techTypeValue, //   1.html 2.js 3.vue 
        title: this.title, //  
        isEdit: state.isEdit, //
        topic_id: state.article.data.topic_id //id
      }).then(res => {
        let data = res.data;
        console.log(data)
        this.$store.commit('ISEDIT',false);
        if (data.status === 1) {
          this.$store.commit('CLEAR_ARTICLE');
          this.$router.push({
            path: '/article/'
          });
        } else {
          this.$message.error({
            showClose: true,
            message: ""
          });
        }
      });
    },
    
  },
  beforeCreate() {
    //,BUG
    //isEdit,
    //$route.params.topic_id,
    if ( !state.isEdit || !Object.keys(this.$route.params).length ) {
      this.$store.commit('CLEAR_ARTICLE');
    }
  }
};
</script>

<style scoped>
#write {
  min-width: 1100px;
  margin: 30px auto;
}
#write header {
  min-width: 1100px;
}
.show {
  position: absolute;
  left: 0;
  top: 0;
}

.indexContainer {
  max-width: 1100px;
  max-width: 1400px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin: 30px auto;
}

.btnsContainer {
  position: absolute;
  z-index: 10;
  right: 60px;
  bottom: -70px;
  height: 50px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

/* .btnsContainer .btn {
    display: inline-block;
    border: 1px solid #ccc;
    margin-right: 10px;
    box-sizing: border-box;
    padding: 0 10px;
    background: #fff;
    font-size: 12px;
    height: 25px;
    line-height: 25px;
    cursor: pointer;
    -moz-user-select: none;
    -o-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none; }
    .btnsContainer .btn:active {
      opacity: 0.8;
      background: lightblue; } */

.maskContainer {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.maskContainer .contentContainer {
  width: 60%;
  height: 60%;
  background: #fefefe;
  padding: 20px;
  box-sizing: border-box;
  position: relative;
}
.maskContainer .contentContainer .showAreaContainer {
  height: 100%;
  width: 100%;
  outline: none;
  background: #eee;
  display: block;
  resize: none;
  padding: 10px;
  box-sizing: border-box;
}
.maskContainer .contentContainer .closeBtnContainer {
  position: absolute;
  height: 30px;
  width: 30px;
  right: -40px;
  top: -40px;
  border: 1px solid #fff;
  border-radius: 50%;
}
.maskContainer .contentContainer .closeBtnContainer::before {
  content: '';
  position: absolute;
  width: 70%;
  height: 2px;
  display: bblock;
  background: #fff;
  left: 15%;
  top: calc(50% - 1px);
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
}
.maskContainer .contentContainer .closeBtnContainer::after {
  content: '';
  position: absolute;
  width: 70%;
  height: 2px;
  display: bblock;
  background: #fff;
  left: 15%;
  top: calc(50% - 1px);
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
}

.editorContainer {
  width: 90%;
  height: 650px;
  border: 1px solid #ddd;
}

/*# sourceMappingURL=aa.css.map */
</style>


